<template>
  <div class="branch">
    <branch-options @search="search" ref="branchOptions"/>
    <branch-table :loading="loading" @search="search" :data="data"/>
    <div class="demonstration-box">
        <!-- <span class="demonstration">显示总数</span> -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageParams.size"
          layout="total, prev, pager, next"
          :total="pageParams.totalCount"
        ></el-pagination>
      </div>
  </div>
</template>

<script>
import BranchOptions from './controls/branchOptions'
import BranchTable from './controls/branchTable'
import queryString from "querystring"
import page from 'data/page'
let urls = ['/physicalChild','/chinaMedChild','/dentistryChild'];
export default {
  name: 'branch',
  mixins: [page],
  data() {
    return {
      data:[], //table数据
      currentPage: 1,
      loading: false
    }
  },
  components: {
    BranchOptions,
    BranchTable
  },
  mounted(){
    this.search();
  },
  methods:{
    search(){  //搜索
      if( !this.$route.query ) return;
      this.loading = true;
      let { code } = this.$route.query;
      const optionParams = this.$refs.branchOptions.getData();
      const tableParams = {
        size: 20,
        page: this.currentPage -1
      };
      let params = this.lodash.assign({}, optionParams, tableParams);
      const param = queryString.stringify(params || {});
      let url = urls[code];
      this.api.get(`${url}?${param}`).then((res)=>{
        this.loading = false;
        if(!res.data) return;
        this.data = res.data.list;
        this.setPage(res.data)
        this.dataInit();
      }).catch((err) => {
        console.log(err)
        this.$message(err.message)
      })
    },
    dataInit(){ //数据初始化
      this.data.map((item) => {
        item.area = item.area || item.provinceValue + item.cityValue + item.countryValue
      })
    }
  },
  watch: {
    currentPage() {
      this.search()
    }
  }
}
</script>
 
